.main_div{
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  
  
 
}

.center_div{
  
  background-color: rgb(255, 242, 242);
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
  width:340px;
  border: blueviolet 4px solid;
  
  
  height:600px;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.taskcontain{
  height:235px;
  overflow: scroll;
  overflow-x: hidden;
  background: rgb(255, 255, 255);
  margin-left: 10px;
  margin-top: 10px;
  border-radius: 8px;
  
}
h1{
  
  color: rgb(255, 255, 255);
  background: rgb(77, 25, 189);
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0 , 0, 0 , 0.3);
  border-radius:100% 0% 100% 0% / 0% 100% 0% 100%  ;
}

p{
  font-size: 12px;
}

h3 {
 color: rgb(38, 3, 115);
 font-size:20px;
}
h2{
  color: rgb(77, 25, 189);
  font-size:30px;
  border-top: 2px solid rgb(77, 25, 189);
  border-bottom:  2px solid rgb(77, 25, 189);
  background-color: white;
  padding-bottom: 6px;
 }

 input{
  outline: none;
  border-bottom: 2px rgb(143, 127, 233) solid;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  font-size: 20px;
  height: 30px;
  
  top: 10px;
  font-weight: 500;
  width: 60%;
  font-size: 20px;
  background-color: transparent;
  color:rgb(77, 25, 189);
  transition-duration: 1s;
  transition-property: all;
}

input:focus{

 
  color: rgb(47, 47, 167);
 transition: 1s ease-in-out all;

}

button{
  min-height: 40px;
  width: 40px;
  color: rgb(255, 255, 255);
  font-size: 35px;
  border-radius: 55% 45% 60% 40% / 30% 70% 30% 70% ;
  border: none;
  background-color: rgb(77, 25, 189);
  border-color: transparent;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 50px -8px #ffffff;;
  transition-property: all;
  transition-duration: .4s;
  cursor: pointer;
  opacity: 1.5;

}


button:hover {
  
 opacity: .5;

  
}

ol li {
  transition-duration: .9s;
  transition-property: all;
  padding-left: 15px;
  
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #ffffff;
  text-transform: capitalize;
  cursor:default;
  
  


}


.todo_style{
  
  display: flex;
  flex-direction:row;
  justify-content: left;
  align-items: center;
  transition: .4s ease--in-out all;
  background-color: rgb(123, 128, 233); 
  border-radius: 8px;
  margin-right: 20px;
  margin-bottom: 10px;
}

.usertask{
  width: 300px;
  

}
.iconcontainer{

  border: 1px silver solid;
  height: 40px;
  width: 10%;
  align-items: center;
  
  padding-top: 8px;
  padding-left: 24px;
  background-color: teal;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;

}
 .fa-times {
   font-size: 22px;
  width:28px;
  height: 28px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color:rgb(187, 37, 37);
  border-radius: 50%;
  margin-right: 10px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  transition-duration: .5s;
  transition-property: all;
  float: right;
  cursor: pointer;
  
}

 .fa-times:hover {
  color: rgb(187, 37, 37);
  background: #ffffff;
  


  
}

@media (max-width:299px){

  .taskcontain{
    height: 180px;
  }
}
@media (max-width:240px){

  .taskcontain{
    height: 150px;
  }
}